import React, { Component } from 'react';

import { Card } from 'primereact/components/card/Card';

class ResultList extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentDidMount() {

  }

  render() {
    let sum = this.props.list.reduce((sum , item)=>{ return sum += item.result.sum },0);
    let cnt = this.props.list.reduce((sum , item)=>{ return sum += item.result.count },0);
    return (
      <div>
        {
          this.props.list.length ?
            <Card title="Feldolgozsi napl">
              <div class="ui-g">
                <div class="ui-g-12 ui-md-12 ui-lg-12 ui-g-nopad">
                  <div className="ui-g-6">Tblzat neve</div>
                  <div className="ui-g-3">Darabszm</div>
                  <div className="ui-g-3 text-right">sszeg (Ft)</div>
                </div>
                {this.props.list.map((item,idx) => 
                  <div  key={idx} class="ui-g-12 ui-md-12 ui-lg-12 ui-g-nopad">
                    <div className="ui-g-6">{item.filename}</div>
                    <div className="ui-g-3">{item.result.count}</div>
                    <div className="ui-g-3 text-right">{item.result.sum.toString().replace(/\d{1,3}(?=(\d{3})+(?!\d))/g, "$&,")}</div>
                  </div>
                )}
                <div class="ui-g-12 ui-md-12 ui-lg-12 ui-g-nopad">
                  <div className="ui-g-6">sszesen</div>
                  <div className="ui-g-3">{cnt}</div>
                  <div className="ui-g-3 text-right">{sum.toString().replace(/\d{1,3}(?=(\d{3})+(?!\d))/g, "$&,") }</div>
                </div>
              </div>

            </Card>
            : null
        }
      </div>
    );
  }
}

export default ResultList;



