import React, { Component } from 'react';
import logo from './broom.svg';

import 'primereact/resources/themes/omega/theme.css';
import 'primereact/resources/primereact.css';
import 'font-awesome/css/font-awesome.css';
import './App.css';

import  ResultList  from './result-list.js';

import { Button } from 'primereact/components/button/Button';
import { ProgressBar } from 'primereact/components/progressbar/ProgressBar';
import { ProgressSpinner } from 'primereact/components/progressspinner/ProgressSpinner';
import { Card } from 'primereact/components/card/Card';
import { Dialog } from 'primereact/components/dialog/Dialog';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      file: null,
      files: [],
      progress: 0,
      state: {
        results:[]
      },
      modal: false,
      spinner: false
    };
  }

  componentDidMount() {

  }

  inputFile(event) {
    console.log(event);
    if (event.target.id == 'hanga-rendeles') {
      this.setState({
        file: event.target.files[0]
      })
    }
    else {
      let files = [];
      for (let i = 0; i != event.target.files.length; i++) {
        files.push(event.target.files[i]);
      }
      this.setState({
        files: files
      })
    }
  }

  start(event) {
    this.setState({ spinner: true });
    this.upload(this.state.file, '/api/sheets/order')
      .then(() => {
        return this.upload(this.state.files, '/api/sheets/client');
      })
      .then(() => {
        return this.simpleGet('/api/sheets/start')
      })
      .then(() => {
        return this.simpleGet('/api/sheets/status')
      })
      .then(responseData => {
        console.log(responseData);
        this.fullProgress = responseData.progress;
        this.timer = setInterval(this.refresh.bind(this), 1000);

        return responseData;
      })
      .catch((err) => {

        // HIBAKEZELS !!!
        this.handleError();
        console.error(err);
      })
  }

  refresh() {
    // TIMEOUT
    this.simpleGet('/api/sheets/status')
      .then((result) => {
        console.log(result);
        this.setState(
          {
            progress: Math.round((this.fullProgress - result.progress) / this.fullProgress * 100),
            state: result
          }
        );

        if (result.error) {

          // HIBAKEZELS !!!
          this.handleError();
          clearInterval(this.timer);
        }

        if (result.progress == 0) {
          clearInterval(this.timer);
          this.setState({ spinner: false });
        }
      })
      .catch((err) => {

      })
  }

  simpleGet(url) {
    return new Promise((resolve, reject) => {
      let req = new XMLHttpRequest();
      req.open("GET", url);
      req.onload = function (event) {
        if (event.target.status >= 400) {
          reject(new Error(event.target));
        }
        else {
          var result;
          try {
            result = JSON.parse(event.target.response);
          }
          catch (err) {
            result = (event.target.response);
          }
          resolve(result)
          console.log(result);
        }
      };
      // req.onerror = function(err) {
      //   reject( new Error (err) ) ;
      // };
      req.send();
    });
  }

  upload(data, url) {
    return new Promise((resolve, reject) => {
      let formData = new FormData();
      if (!data.length) {
        formData.append('sheet', data);
      }
      else {
        data.forEach(file => {
          formData.append('sheet', file);
        })
      }

      let req = new XMLHttpRequest();
      req.open("POST", url);
      req.onload = function (event) {
        resolve(event.target)
        console.log(event.target.responseText);
      };
      req.onerror = function (err) {
        reject(new Error(err));
      };
      req.send(formData);
    });
  }

  handleError() {
    this.setState({ progress: 0, spinner: false });
    this.openModal();
  }

  openModal() {
    this.setState({ modal: true })
  }

  closeModal() {
    this.setState({ modal: false })
  }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">hanga</h1>
        </header>
        <div class="ui-g max-width">

          <div class="ui-g-12 ui-md-6 ui-lg-6">
            <Card className="minHeight100" title="Tblzatok">
              {/* <h3>Rendels</h3> */}
              <div class="file-uploader-wrapper">
                <div class="ui-button ui-corner-all">Rendelsi tblzat feltltse</div>
                <input
                  class="ui-button ui-button-secondary ui-button ui-corner-all"
                  onInput={this.inputFile.bind(this)}
                  type="file"
                  id="hanga-rendeles"
                  name="files[]"
                  accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
                />
              </div>
              <div class="file-list">
                {this.state.file ? <p>{this.state.file.name}</p> : null}
              </div>

              {/* <h3>Vsrli</h3> */}
              <div class="file-uploader-wrapper">
                <div class="ui-button ui-corner-all">Vsrli tblzatok feltltse</div>
                <input
                  class="ui-button ui-button-secondary ui-button ui-corner-all"
                  onInput={this.inputFile.bind(this)}
                  type="file"
                  id="vasarloi-rendelesek"
                  name="files[]"
                  multiple
                  accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
                />
              </div>
              <div class="file-list">
                {this.state.files.map((file,idx) => { return <p key={idx}> {file.name}</p> })}
              </div>
            </Card>
          </div>
          <div class="ui-g-12 ui-md-6 ui-lg-6">
            <Card className="minHeight100" title="Feldolgozs" >
              <div class="row btn-w80px">
                <Button
                  disabled={!this.state.file || !this.state.files.length}
                  type="button"
                  label="Indts"
                  onClick={this.start.bind(this)} />
                {
                  this.state.spinner ?
                    <ProgressSpinner strokeWidth="6" style={{ width: '25px', height: '25px', top: '7px', float: 'right' }} />
                    : null
                }
              </div>

              <div class="row">
                <ProgressBar value={this.state.progress}></ProgressBar>
              </div>
              <div class="row btn-w80px">
                {
                  !this.state.state.error && this.state.state.status == 'export done!' ?
                    <a href="/api/sheets/result" class="ui-button ui-widget ui-corner-all ui-button-success ui-state-default">Letlts</a> : null
                }
              </div>
            </Card>
          </div>
          <div class="ui-g-12 ui-md-12 ui-lg-12">
            <ResultList list={this.state.state.results}></ResultList>
          </div>
        </div>


        <Dialog header="Kedveztlen llapot" visible={this.state.modal} width="350px" modal={true} minY={120} footer="" onHide={this.closeModal.bind(this)}>
          <div class="text-center">
            <p>Upsz! </p>
            <p>Valami nem sikerlt...</p>
          </div>
        </Dialog>

        {/* <Button label="Show" icon="fa-external-link-square" onClick={this.openModal.bind(this)} /> */}

      </div >
    );
  }
}

export default App;



